.breadcrumb {
    &-item {
        a {
            color: $textGrey;
        }
        a {
            text-decoration: none;
        }
    }
}

.filter_results {
    .filter_nav {
        border-right: 1px solid #F6F6F6;
        max-width: 285px;
        h5 {
            font-size: 20px;
            color: $textBlack;
            margin-bottom: 1rem;
        }
        .title {
            background-color: $darkGreen;
            color: #fff;
            height: 35px;
        }
    }
    .results {
        h1 {
            font-size: 32px;
            color: $textBlack;
            &+span {
                @extend %smallText;
            }
        }
        .sortBy {
            &>span {
                @extend %smallText;
            }
            .nav-link {
                @extend %smallText;
                border: 1px solid transparent;
                &.active {
                    color: $colorPrimary;
                    border-color: currentColor;
                }
            }
        }
    }
}

.pagination {
    .page-link {
        border-radius: 50%;
        @extend %smallText;
        margin: 0 10px;
        width: 30px;
        height: 30px;
        padding: 0;
        text-align: center;
        border: 1px solid $textBlack;
    }
    [aria-label] {
        border: none;
    }
    .active {
        .page-link {
            border-radius: 50%;
            background-color: $textBlack;
            color: #fff;
            margin: 0 10px;
        }
    }
}

input[type="range"] {
    position: relative;
    -webkit-appearance: none;
    -moz-appearance: none;
    display: block;
    width: 80%;
    height: 8px;
    background-color: #d5d5d5;
    border-radius: 8px;
    outline: none;
}

input[type="range"]::-webkit-slider-runnable-track {
    -webkit-appearance: none;
    height: 8px;
}

input[type="range"]::-moz-track {
    -moz-appearance: none;
    height: 8px;
}

input[type="range"]::-ms-track {
    appearance: none;
    height: 8px;
}

input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 20px;
    width: 20px;
    background-color: #3264fe;
    border-radius: 50%;
    cursor: pointer;
    margin-top: -6px;
    border: none;
}

input[type="range"]::-moz-range-thumb {
    -webkit-appearance: none;
    height: 20px;
    width: 20px;
    background-color: #3264fe;
    border-radius: 50%;
    cursor: pointer;
    margin-top: -6px;
    border: none;
}

input[type="range"]::-ms-thumb {
    appearance: none;
    height: 20px;
    width: 20px;
    background-color: #3264fe;
    border-radius: 50%;
    cursor: pointer;
    margin-top: -6px;
    border: none;
}

input[type="range"]:active::-webkit-slider-thumb {
    background-color: #ffffff;
    border: 3px solid #3264fe;
}

#slider-value {
    width: 13%;
    position: relative;
    background-color: #3264fe;
    color: #ffffff;
    text-align: center;
    font-family: "Roboto Mono", monospace;
    padding: 10px 0;
    border-radius: 5px;
}